@import url('https://fonts.googleapis.com/css2?family=Exo:wght@100;200;400;500&family=Lato&display=swap');

@tailwind base;
@tailwind components;
@tailwind utilities;


.r-polygon {
    clip-path: polygon(100% 31%, 0% 100%, 100% 100%);
    background-color: #ff6d6d;
}

.l-polygon {
    clip-path: polygon(0 0, 21% 0, 100% 100%, 0% 100%);
    background-color: #ff6d6d;
}

.footer-triangle {
    clip-path: polygon(100% 0, 16% 0, 100% 75%);
    background-color: #ff6d6d;
}